import React from "react";
import { Route, Redirect } from "react-router-dom";

export default function AuthRoute(props) {
  function isLogin() {
    if (localStorage.getItem(1)) {
      console.log(" ");
      return true;
    } else {
      console.log(" ");
      return false;
    }
  }
  return (
    <Route
      render={() =>
        isLogin() ? <Redirect to="/mypage" /> : <Redirect to="/" />
      }
    />
  );
}
